import React, { Component } from 'react';
import pd_png from '../../../assets/image/pd.png';
import { Carousel, Button, ListView } from 'antd-mobile';
import CustomWhiteSpace from '../../../component/customWhiteSpace';
import fullScreen from '../../../component/fullScreen';
import InfoList from '../../../component/infoList';
import TitleContent from '../../../component/titleContent';
import NoResult from '../../../component/noResult';
import { request } from '../../../request';
import api from '../../../request/api';
import styles from './index.less';

export default fullScreen(class designer extends Component {
    state = {
        pageNo: 1,
        pageSize: 10,
        hasMore: true,
        keyword: '',
        dataBlobs: [],
        dataSource: new ListView.DataSource({
            rowHasChanged: (row1, row2) => row1 !== row2
        }),
        isLoading: false,
        height: 0,
    }

    componentDidMount() {
        const { location } = this.props,
            { state = {} } = location,
            { name = '设计师详情' } = state;
        document.title = name;
        this.getCaseList()
    }

    getCaseList = ({
        pageNo = this.state.pageNo,
        keyword = this.state.keyword,
        dataBlobs = this.state.dataBlobs,
    } = {}) => {
        const { pageSize, dataSource } = this.state;
        this.setState({ isLoading: true, keyword })
        request({ url: api.pageCase, data: { pageNo, pageSize, keyword, sceneId: '' } }).then(res => {
            const { list, pageTurn } = res,
                { nextPage, rowCount } = pageTurn,
                _dataBlobs = [...dataBlobs, ...list];
            this.setState({
                height: !_dataBlobs.length ? 45 : _dataBlobs.length === 1 ? 140 : 300,
                hasMore: _dataBlobs.length >= rowCount ? false : true,
                pageNo: nextPage,
                dataBlobs: _dataBlobs,
                dataSource: dataSource.cloneWithRows([..._dataBlobs]),
                isLoading: false,
            })
            pageNo === 1 && this.lv.scrollTo(0, 0)
        }).catch(err => { this.setState({ isLoading: false }) })
    }

    onEndReached = (event) => {
        const { isLoading, hasMore } = this.state;
        if (isLoading || !hasMore)
            return;
        this.getCaseList()
    }

    render() {
        const { dataBlobs, dataSource, height } = this.state;
        return (
            <div className={styles.wrapper}>
                <div className={styles.wrapper_container}>
                    <Carousel
                        autoplay={false}
                        infinite
                        className='carousel_common'
                    >
                        {[pd_png, pd_png, pd_png].map((val, index) => (
                            <span
                                key={index}
                                className='carousel_common_item'
                            >
                                <img
                                    src={val}
                                    alt=""
                                    onLoad={() => {
                                        // fire window resize event to change height
                                        window.dispatchEvent(new Event('resize'));
                                    }}
                                />
                            </span>
                        ))}
                    </Carousel>
                    <div className={styles.wrapper_container_info}>
                        <div className={styles.wrapper_container_info_avator}>
                            <img src={pd_png} alt='' />
                        </div>
                        <ul className={styles.wrapper_container_info_detail}>
                            <li className='titleFontSizeC'>Funcsxa</li>
                            <li className='textFontSizeC shallowGreyColor'><i className='iconfont icon-address' />广州 | 梦天旗舰店</li>
                        </ul>
                        <InfoList
                            data={[
                                { label: '设计经验', value: '10年' },
                                { label: '擅长风格', value: '现代简约 欧式' },
                                { label: '设计理念', value: '设计理念设计理念设计理念设计理念设计理念设计理念' },
                            ]}
                        />
                    </div>
                    <CustomWhiteSpace />
                    <TitleContent title='设计作品'>
                        <div>
                            <div>全部 整装 主卧 次卧</div>
                            <ListView
                                dataSource={dataSource}
                                renderHeader={() => dataBlobs.length ? null : <NoResult />}
                                renderRow={(item, sectionID, index) => { console.log(item); return <div>xxxxxxx</div> }}
                                style={{
                                    height,
                                }}
                                onEndReached={this.onEndReached}
                                onEndReachedThreshold={30}
                            />
                        </div>
                    </TitleContent>
                </div>
                <div className={styles.wrapper_footer}>
                    <Button type='warning'>预约量房</Button>
                </div>
            </div>
        );
    }
})